* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Inter", "sans-serif";
}
body {
  background-color: #171717;
}
.header {
  background-color: #f7b42c;
  background-image: linear-gradient(315deg, #f7b42c 0%, #fc575e 74%);
  color: white;
  padding: 1rem 3rem;
  height: 10vh;
  display: flex;
  align-items: center;
}
.header .right {
  margin-left: auto;
}
.form {
  padding: 2rem 3rem;
  display: grid;
  grid-template: minmax(40px, 8vh) minmax(30px, 8vh) 1fr/ 1fr 1fr;
  
  gap: 2rem;
}
.form .btn {
  background-color: #f7b42c;
  background-image: linear-gradient(315deg, #f7b42c 0%, #fc575e 74%);
  border: none;
  border-radius: 10px;
  justify-self: center;
  cursor: pointer;
  color: white;
  
  font-weight: 600;
  font-size: 1.4rem;
  padding: 0.5rem 2.5rem;
  grid-column: 1 / -1;
  transition: 0.1s all linear;
}
.btn:hover {
  background-image: linear-gradient(15deg, #f7b42c 0%, #fc575e 74%);
}
.btn:active {
  border: 2px solid white;
}
.form .input {
  border: none;
  border-radius: 5px;
  text-indent: 10px;
  transition: 0.1s all linear;
}
.input:focus {
  border: none;
  outline: none;
}
.input:hover,
:focus {
  border: 4px solid;
  border-image-source: linear-gradient(315deg, #f7b42c 0%, #fc575e 74%);
  border-image-slice: 1;
  border-radius: 5px;
}
.meme-img {
  max-width: 80%;
}
.meme-img-container {
  position: relative;
  min-width: 350px;
  max-width: 50%;

  grid-column: 1/-1;
  justify-self: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
.meme-text {
  position: absolute;

  width: 80%;
  color: white;
  text-align: center;
  font-size: 2.2rem;
  font-weight: bold;
  font-style: italic;
  text-shadow: 0 0 4px black, 0 0 4px black, 0 0 4px black, 0 0 4px black,
    0 0 4px black, 0 0 4px black, 0 0 4px black, 0 0 4px black, 0 0 4px black,
    0 0 4px black, 0 0 4px black, 0 0 4px black, 0 0 4px black, 0 0 4px black,
    0 0 4px black, 0 0 4px black, 0 0 4px black, 0 0 4px black, 0 0 4px black,
    0 0 4px black, 0 0 4px black, 0 0 4px black, 0 0 4px black;
  left: 50%;
  transform: translate(-50%, -50%);
}
.top-text {
  top: 10%;
}
.bottom-text {
  bottom: 10%;
}
